@import url(https://fonts.googleapis.com/css2?family=Jua&display=swap);
.chat-background{height:calc(100%);width:100%;background:#c0392b;background:linear-gradient(45deg, #8e44ad, #c0392b)}.chat-box{width:100%;height:100%;padding:15px;font-family:'Jua', sans-serif}.chat-box .chat-logs{width:100%;height:80vh;background:rgba(255,255,255,0.2);border-radius:20px 20px 0 0;margin:auto;padding:10px;overflow-y:scroll}.chat-box .chat-logs .chat{display:flex;flex-flow:row wrap;align-items:flex-start;margin-bottom:10px}.chat-box .chat-logs .chat .user-photo{width:65px;height:65px;background:grey;border-radius:50%}.chat-box .chat-logs .chat .chat-msg{width:calc(100% - 90px);margin:5px 10px 10px 10px;padding:15px;border-radius:10px;font-size:1.2em}.chat-box .chat-logs .chat .friend{background:rgba(0,0,0,0.4);color:white}.chat-box .chat-logs .chat .me{background:rgba(0,0,0,0.4);color:white;order:-1}.chat-box .chat-form{width:100%;height:11vh;background:rgba(0,0,0,0.5);border-radius:0 0 20px 20px;display:flex;flex-flow:row nowrap;align-items:center;justify-content:center;margin:auto}.chat-box .chat-form .chat-text{height:70%;width:73%;padding:10px;overflow-x:scroll;font-size:1em;border-radius:10px;background:rgba(255,255,255,0.8);border:none;font-family:'Jua', sans-serif !important}.chat-box .chat-form .chat-btn--snd{height:55px;width:55px;margin-left:15px;border-radius:50%;background:#cb356b;background:linear-gradient(to right, #bd3f32, #cb356b)}.chat-box .chat-form .chat-btn--snd i{font-size:2.2em;color:rgba(255,255,255,0.7)}@media (min-width: 1300px){.chat-background{height:calc(100% - 57px);width:100%}.chat-background .chat-logs{width:40%;height:80vh}.chat-background .chat-form{width:40%}}

